/* THIS HOOK SHOULD BE USED ONLY ONCE ON A PAGE. WHEELING PROPERTY CANNOT BE IN STATE, BECAUSE THAT IS DELAYED IN REACT,
*  HENCE MOUSE WILL TRIGGER A LOT MORE EVENTS. BUT BECAUSE WHEELING IS NOW A MODULE PROP, MULTIPLE useScroll WILL
*  INFLUENCE THIS SAME PROPERTY. NO GO. RESOLVE & LEAVE IT, YOU DON'T WANT 2 SCROLLS IN 1 PAGE ANYWAY */
import {useEffect, useState} from "react";

const TIME_TO_WAIT_FOR_NEW_SCROLL_EVENT_IN_MILLIS = 50;

const WHEEL_EVENT = 'wheel';
const TOUCH_START_EVENT = 'touchstart';
const TOUCH_END_EVENT = 'touchend';
let wheeling: number | undefined = undefined;

function usePageItemsScroll(totalItemsToScrollThrough: number, initialItemIndex: number = 0): {activeIndex: number, previousIndex: number | undefined} {
    const [activeIndex, setActiveIndex] = useState(initialItemIndex);
    const [previousIndex, setPreviousIndex] = useState<number | undefined>(undefined);

    function next() {
        setPreviousIndex(activeIndex);
        setActiveIndex(activeIndex + 1);
    }
    function prev() {
        setPreviousIndex(activeIndex);
        setActiveIndex(activeIndex - 1);
    }

    function touchStartEvent(e: TouchEvent) {
        const yStart = e.changedTouches[0].pageY;

        function touchEndEvent(e) {
            window.removeEventListener(TOUCH_END_EVENT, touchEndEvent);
            const treshold = e.changedTouches[0].pageY - yStart;
            if (Math.abs(treshold) < 15) {
                return;
            }
            if (treshold > 0) {
                next();
            } else {
                prev();
            }
        }
        window.addEventListener(TOUCH_END_EVENT, touchEndEvent);
    }

    function wheelEvent(e) {
        if (wheeling === undefined) {
            if (e.deltaY > 0 && activeIndex < totalItemsToScrollThrough - 1) {
                next();
            } else if (e.deltaY < 0 && 0 < activeIndex) {
                prev();
            }
        }
        clearTimeout(wheeling);
        wheeling = window.setTimeout(() => {
            wheeling = undefined;
        }, TIME_TO_WAIT_FOR_NEW_SCROLL_EVENT_IN_MILLIS);
    }

    // inefficient, needs a single time (so deps [], but had issue and code will not stay probably :) )
    useEffect(() => {
        window.addEventListener(WHEEL_EVENT, wheelEvent);
        window.addEventListener(TOUCH_START_EVENT, touchStartEvent);
        return () => {
            window.removeEventListener(WHEEL_EVENT, wheelEvent);
            window.removeEventListener(TOUCH_START_EVENT, touchStartEvent);
        }
    }, [activeIndex]);

    return {
        activeIndex,
        previousIndex,
    };
}

export default usePageItemsScroll;